import _ from 'lodash'
import angular from 'angular'

export default angular
  .module('CollectionPreviewsControllerComponentModule', [])
  .controller(
    'CollectionPreviewsController',
    function (
      $rootScope,
      $scope,
      auth,
      collections,
      config,
      mobileService,
      registrationService,
      titleService,
      tree
    ) {
      $scope.auth = auth
      $scope.config = config
      $scope.collections = collections
      $rootScope.mobile = mobileService
      $scope.tree = tree
      $scope.registrationService = registrationService

      // Generate breadcrumbs

      $scope.breadcrumbs = []

      _.each(tree.parents, parent => {
        $scope.breadcrumbs.push({
          label: parent.name,
          state: parent.state,
          params: parent.params,
        })
      })

      if (tree.current.name) {
        $scope.breadcrumbs.push({
          label: tree.current.name,
          active: true,
        })

        titleService.setTitle(tree.current.name)
      }
    }
  )
